const SlotIcon = ({ icon }: { icon: string }) => {
    const spriteMap: Record<string, { x: number; y: number }> = {
        j: { x: 0, y: -8 },
        a: { x: -160, y: -8 },
        d: { x: -320, y: -8 },
        k: { x: 0, y: -165 },
        w: { x: -160, y: -165 },
        q: { x: -320, y: -165 },
        empty: { x: 0, y: -100 }, // Placeholder for empty cells
    }

    const { x, y } = spriteMap[icon] || spriteMap['empty']
    return (
        <div
            className="h-36 w-36 bg-no-repeat"
            style={{
                backgroundImage: "url('/images/sprites-reels.webp')",
                backgroundPosition: `${x}px ${y}px`,
            }}
        ></div>
    )
}

export default SlotIcon
